
import { defineComponent, inject, ref, computed } from 'vue';
import Modal from '@/components/Modal.vue';
import { Datastore, Receipt } from '@/datastore';
import { DateTime } from 'luxon';

export default defineComponent({
  components: {
    Modal,
  },
  setup () {
    const datastore = inject<Datastore>('datastore')!
    const receipts = ref<Receipt[]>([])
    datastore.receipt.getReceipts(undefined, undefined)
      .then(x => {
        receipts.value = x
      })
    const receiptsTableData = computed(() => receipts.value.map(x => ({
      ...x,
      date: x.date.toFormat('dd/MM/yyyy')
    })))
    
    const startDate = ref<string>()
    const endDate = ref<string>()

    async function onSearch() {
      receipts.value = await datastore.receipt.getReceipts(startDate.value ? DateTime.fromISO(startDate.value) : undefined, endDate.value ? DateTime.fromISO(endDate.value) : undefined)
    }

    return {
      receiptsTableData,
      startDate,
      endDate,
      onSearch,
    }
  },
});
